import "moment";
import "devalue";
import "../../../chunks/index2.js";
import "../../../chunks/monetize.js";
const MenuRoutes = [
  {
    name: "Ana Sayfa",
    path: "/home",
    role: ["user"]
  },
  {
    name: "Satışlar",
    path: "/sales",
    role: ["user"]
  },
  {
    name: "Ana Sayfa",
    path: "/dashboard",
    role: ["admin"],
    icon: "dashboard"
  },
  {
    name: "Canlı Veri",
    path: "/live",
    role: ["admin"],
    icon: "graph"
  },
  {
    name: "Rapor",
    path: "/report",
    role: ["admin"],
    icon: "list"
  },
  {
    name: "Ayarlar",
    path: "/settings",
    role: ["admin", "user"],
    icon: "settings"
  },
  {
    name: "Çıkış",
    path: "/logout",
    role: ["user", "admin"],
    icon: "logout"
  }
];
const load = async ({ locals }) => {
  const roles = ["user", "admin", "guest"];
  const menu = MenuRoutes.filter((item) => item.role.includes(roles[locals.isadmin]));
  return {
    role: roles[locals.isadmin],
    menu,
    username: locals.username
  };
};
export {
  load
};
